  text-decoration: none;
  letter-spacing: 0.025rem;
  padding: var(--space-sm) var(--space-md) var(--space-sm) var(--space-md);
  border: 1px solid transparent;
  cursor: pointer;
  transition: all 0.3s var(--easing); }
  .btn:visited {
    color: white; }
  .btn:hover {
